}

/* ---------------------------------------------------
      CONTENT STYLE
  ----------------------------------------------------- */
.content {
	overflow: auto;
	padding: 0px 20px 20px 20px;
	margin-left: 0;
	height: 100vh;
}
@media only screen and (max-width: 800px) {
	.sidebar.is-open {
		min-width: 180px;
		max-width: 180px;
	}
	.side-menu {
		text-align: center;
	}
}
@media only screen and (max-width: 590px) {
	body {
		overflow: hidden;
	}
	.sidebar {
		z-index: 3;

		height: 100vh;
		position: fixed;
	}
	.sidebar.is-open {
		min-width: 100%;
		max-width: 100%;
		z-index: 3;
		position: fixed;
		height: 100vh;
		margin-left: 0;
		transition: all 0.5s, height 0s;
	}
	.o-nav {
		position: fixed;
		z-index: 1;
		width: 90%;
	}
	.sidebar.is-open > .sidebar-header span {
		display: unset;
		position: fixed;
	}
	.content {
		overflow-x: auto;
		padding: 20px;
		margin-left: 0;
		margin-top: 60px;
		height: 100vh;
		position: fixed;
	}
	li a.dropdown-toggle::after {
		display: inline-block;
		position: relative;
	}
}

