.o-register-container {
  margin: -2rem 0 0 0;
  width: 100%;
  height: auto;
  padding: 1rem 2rem;
  border-radius: 0.8rem;
  background-color: #eef7fc;
}
.o-list-skill-cont {
  height: 10rem;
}
.o-register-container .inputWrapper {
  height: 2rem;
  width: 7rem;
  margin: 1rem auto 3rem auto;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  /*Using a background color, but you can use a background image to represent a button*/
  background-color: #4285f4;
  border-radius: 2px;
}
.o-register-container .inputWrapper:hover {
  background-color: #0d47a1;
}
.o-register-container .fileInput {
  cursor: pointer;
  height: 5rem;
  width: 12rem;
  padding-bottom: 4rem;
  position: absolute;
  left: 0;
  z-index: 99;
  justify-self: center;
  /*This makes the button huge. If you want a bigger button, increase the font size*/
  /*Opacity settings for all browsers*/
  opacity: 0;
  -moz-opacity: 0;
  filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
}

.o-register-container .o-icon-input-text {
  left: 1.6rem;
  top: 0.4rem;
  font-size: 0.9rem;
  position: absolute;
  text-decoration: none;
}
.o-register-container .o-icon-input-text:focus {
  outline: none;
  text-decoration: none;
}

.o-user-register-pic {
  width: 5.5rem;
  height: 5.5rem;
}
.o-accounts-link {
  width: 5.5rem;
  height: 5.5rem;
  opacity: 0.6;
}
.o-register-container .md-outline input[type="text"] + label,
.o-register-container .md-outline textarea + label {
  transform: translateY(-14px) scale(0.8);
  background: #fff;
  height: 1.6rem;
  left: 1px;
  padding-left: 5px;
  padding-bottom: 0px;
  padding-right: 5px;
  margin-left: 0.5rem;
  font-size: 0.8rem;
  color: black;
  position: absolute;
}
.o-register-container .md-outline input[type="text"],
.o-register-container .md-outline textarea {
  border-color: #007bff;
  border-bottom: 0.1px solid #007bff;
}

.o-register-container .md-outline textarea:focus + label {
  color: #007bff;
}
.o-register-container .md-outline textarea:focus {
  border-color: #007bff;
  box-shadow: inset 0 0 0 1px #007bff;
  border-bottom: 0.3px solid #007bff;
}

.o-f {
  max-width: auto;
}

.o-box {
  position: absolute;
  margin-top: 1.5rem;
}

.o-box select {
  background: #fff;
  padding: 0;
  border-radius: 5px;
  width: 250px;
  height: 2.3rem;
  border: 1px solid #007bff;
  font-size: 0.9rem;
  -webkit-appearance: button;
  outline: none;
}

.o-box::before {
  content: "\f0dd";
  font-family: "FontAwesome";
  position: absolute;
  border-radius: 0 5px 5px 0;
  top: 0;
  right: 0;
  width: 2.5rem;
  height: 2.3rem;
  text-align: center;
  line-height: 25px;
  pointer-events: none;
  color: #fff;
  font-size: 28px;
  background: #007ce0;
}
.o-box:hover::before {
  background: #0472ca;
}
.o-skills-select {
  min-width: 18rem;
  max-width: 18rem;
}
.o-skills-select .o-box {
  width: 15.6rem;
  height: 1rem;
  font-size: 0.8rem;
}
.o-col {
  min-width: 17rem;
}
.o-card-select-skill {
  width: 7rem;
  min-width: 7rem;
  height: 7.8rem;
  background-color: #fff;
  border: 1px solid #0472ca;
  padding: 0;
  margin: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.o-selected-skill-img {
  width: 5rem;
  height: 5rem;
  margin: 0.7rem auto auto auto;
  outline: none;
}
.o-selected-skill-img::selection {
  outline: none;
}

.o-x-btn {
  width: 1.5rem;
  height: 1.4rem;
  font-size: 1.5rem;
  color: red;
  background: white;
  margin: 0.6rem 0 0 6.1rem;
  position: absolute;
  padding: 0;
  top: 0;
}

.o-x-btn:hover {
  color: rgb(182, 5, 5);
}
.o-skill-list-cnt {
  min-height: 9rem;
  overflow-x: auto;
  position: relative;
  border: 1px solid #4285f4;
  scrollbar-color: #4285f4 white;
  scrollbar-width: thin;
}
@media only screen and (max-width: 1290px) {
  .o-box {
    margin-bottom: 4rem;
  }
  .o-col {
    padding-bottom: 2rem;
  }
}
@media only screen and (max-width: 890px) {
  .o-col {
    padding-bottom: 2rem;
    min-width: 15rem;
  }
}
@media only screen and (max-width: 590px) {
  .o-register-container {
    margin: 0 0 5rem 0;
    padding: 0rem;
    text-align: center;
  }
  .o-skills-select {
    min-width: auto;
    margin: 0 auto 0 auto;
  }
  .o-col {
    padding: 3px;
    min-width: 17rem;
  }
  .o-f {
    max-width: 11.5rem;
  }
  .o-up-btn {
    max-width: 9rem;
  }
}

.o-skill-list-cnt::-webkit-scrollbar {
  height: 8px;
  background-color: #e0e0e0;
}
.o-skill-list-cnt::-webkit-scrollbar-thumb {
  background: #4285f4;
  border-radius: 3px;
}

.o-skill-list-cnt::-webkit-scrollbar-thumb:hover {
  background: #3a4c99;
}
